/* Fonts */
@font-face {
    font-family: 'PublicSans-Black';
    src: url('../../fonts/PublicSans-Black.woff2') format('woff2'),
        url('../../fonts/PublicSans-Bold.woff') format('woff'),
        url('../../fonts/PublicSans-Black.ttf') format('truetype');
    font-weight: 900;
    font-style: normal;
    font-display: swap;
  }
  
  @font-face {
    font-family: 'PublicSans-Regular';
    src: url('../../fonts/PublicSans-Regular.woff2') format('woff2'),
        url('../../fonts/PublicSans-Regular.woff') format('woff'),
        url('../../fonts/PublicSans-Regular.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
  }
  
  @font-face {
    font-family: 'PublicSans-Bold';
    src: url('../../fonts/PublicSans-Bold.woff2') format('woff2'),
        url('../../fonts/PublicSans-Bold.woff') format('woff'),
        url('../../fonts/PublicSans-Bold.ttf') format('truetype');
    font-weight: bold;
    font-style: normal;
    font-display: swap;
  }

  $__Punlicsans__Regular: 'PublicSans-Regular';
  $__Punlicsans__Black : 'PublicSans-Black';
  $__Punlicsans__Bold : 'PublicSans-Bold';

  $__font__size__10:10px;
  $__font__size__11:11px;
  $__font__size__12:12px;
  $__font__size__13:13px;
  $__font__size__14:14px;
  $__font__size__15:15px;
  $__font__size__16:16px;
  $__font__size__17:17px;
  $__font__size__19:19px;
  $__font__size__18:18px;
  $__font__size__20:20px;
  $__font__size__22:22px;
  $__font__size__24:24px;
  $__font__size__26:26px;
  $__font__size__28:28px;
  $__font__size__30:30px;
  $__font__size__35:35px;
  $__font__size__40:40px;
  $__font__size__50:50px;


  html {
    scroll-behavior: smooth;
  }

body{
    font-family: $__Punlicsans__Bold;
    background-color: $__white; 
    &::-webkit-scrollbar {
      width: 0em;
    }
     
    &::-webkit-scrollbar-track {
      box-shadow: inset 0 0 0px rgba(0, 0, 0, 0);
    }
     
    &::-webkit-scrollbar-thumb {
      background-color: darkgrey;
      outline: 0px solid slategrey;
    }
}

/* Common Class */
.container{
  max-width: 1280px;
  padding: 0px 15px;
  margin: $__auto;
}
/* End */

.site-wrapper{
  padding-top: 84px;
  @include respond-below(custom767){
    padding-top: 150px;
  }
}

/* Header-Section */
  header{
    position: $__fixed;
    top: 0;
    left: 0;
    width: $__full__width;
    z-index: 100;
    .header-wrapper{
      background-color: $__header_bg;
      .header-inner-section{
        display: $__flex;
        align-items: $__center;
        justify-content: space-between;
        max-width: 1280px;
        margin: $__auto;
        padding: 15px;
        @include respond-below(custom767){
          flex-wrap: wrap;
        }
        .logo-Section{
          @include respond-below(custom767){
            flex: 25%;
          }
          .logo{
            img{
              max-width: 50px;
            }
          }
        }
        .navigation-links{
          @include respond-below(custom767){
            order: 3;
            padding: 15px 0px;
            flex: 50%;
          }
          ul{
            list-style: $__none;
            padding: 0;
            margin: 0;
            li{
              display: $__inline__block;
              .nav-link{
                padding: 0px 15px;
                padding-bottom: 15px;
                text-decoration: $__none;
                margin: 0px 5px;
                span{
                  font-size: $__font__size__15;
                  font-family: $__Punlicsans__Bold;
                  color: $__white;
                }
              }
              .active{
                border-bottom: 2px solid $__primary__color;
                color: $__primary__color;
              }
            }
          }
          .search-product{
            position: $__relative;
            width: 630px;
            @include respond-below(custom992){
              width: 100%;
            }
            @include respond-below(custom767){
              width: 100%;
              flex: 100%;
            }
            .search-input{
              background-color: $__white;
              width: $__initial;
              margin: $__auto;
              display: $__flex;
              align-items: $__center;
              padding: 0px 15px;
              .search-icon{
                font-size: $__font__size__20;
                color: rgba(40,116,240,1);
                display: $__flex;
              }
              input[type="text"]{
                width: 95%;
                border: $__none;
                outline: $__none;
                padding: 10px 0px;
                padding-left: 15px;
              }
            }
            .search-drop-down{
              margin-top: 5px;
              background-color: $__white;
              position: $__absolute;
              width: $__full__width;
              .search-filter-data{
                text-decoration: $__none;
                color: rgba(40,116,240,1);
                display: $__none;
                font-size: $__font__size__15;
                font-family: $__Punlicsans__Regular;
                text-transform: capitalize;
                padding: 5px 15px;
              }
            }
          }
        }
        .cart-profile{
          ul{
            list-style: $__none;
            margin: 0;
            padding: 0;
            @include respond-below(custom992){
              text-align: $__right;
            }
            li{
              display: $__inline__block;
              margin: 0px 10px;
              .link-icons{
                text-decoration: $__none;
                .cart-section-header{
                  display: $__flex;
                  align-items: $__center;
                  position: $__relative;
                  img{
                    max-width: 20px;
                  }
                  .cart-lable-sp{
                    font-size: $__font__size__14;
                    font-family: $__Punlicsans__Black;
                    margin-left: 10px;
                    color: $__white;
                  }
                  .cart-product-count{
                    width: 20px;
                    height: 20px;
                    background-color: #b72136;
                    font-size: $__font__size__12;
                    color: $__white;
                    border-radius: 50%;
                    position: $__absolute;
                    top: -10px;
                    left: 7px;
                    display: $__flex;
                    align-items: $__center;
                    justify-content: $__center;
                  }
                }
                img{
                  max-width: 25px;
                }
              }
            }
          }
        }
      }
    }
  }
/* End */

/* Home-Page */
  .home-Page-wrapper{
    
    .inner-section{
      .product-section{
        padding: 5% 0;
        .categories{
          box-shadow: rgba(145, 158, 171, 0.24) 0px 0px 2px 0px, rgba(145, 158, 171, 0.24) 0px 16px 32px -4px;
          padding: 10px;
          position: $__relative;
          overflow: $__hidden;
          //background-color: rgb(40,116,240,0.2);
          transition: 0.5s ease;
          @include respond-below(custom992){
            margin-bottom: 25px;
          }
          .img-section{
            width: $__full__width;
            img{
              max-width: $__full__width;
              width: $__full__width;
              object-fit: $__cover;
              height: 300px;
              object-position: $__top;
            }
          }
          .categories-head{
            margin: 0;
            font-size: $__font__size__17;
            font-family: $__Punlicsans__Bold;
            color: $__black;
            padding: 20px 0px;
            text-align: $__center;
          }
          .view-all{
            position: $__absolute;
            top: calc(100%  - 61% );
            left: calc(100% - 65%);
            z-index: 10;
            opacity: 0;
            .view-all-btn{
              text-decoration: $__none;
              font-size: $__font__size__15;
              font-family: $__Punlicsans__Bold;
              padding: 15px 10px;
              z-index: 10;
              display: $__flex;
              background-color: rgb(40,116,240,1);
              border-radius: 8px;
              color: $__white;
            }
          }
          &::after{
            content: "";
            display: $__block;
            width: $__full__width;
            height: 100%;
            background-color: rgb(40,116,240,0.4);
            position: $__absolute;
            top: 0;
            left: 0;
            z-index: 0;
            opacity: 0;
            transition: 0.5s ease;
          }
          &:hover{
            &::after{
              opacity: 1;
            }
            .view-all{
              opacity: 1;
            }
          }
        }
      }
      .banner-section{
        padding: 50px 0px;
        background-color: rgba(40,116,240,0.5);
        .banner-col{
          display: $__flex;
          align-items: $__center;
          flex-wrap: wrap;
          margin: 15% 0;
          @include respond-below(custom992){
            margin: 1% 0;
          }
          @include respond-below(custom767){
            margin: 5% 0;
            margin-bottom: 15%;
            justify-content: $__center;
          }
          h2{
            font-size: $__font__size__40;
            font-family: $__Punlicsans__Bold;
            color: $__white;
            margin: 0;
            padding-bottom: 15px;
            @include respond-below(custom767){
              font-size: $__font__size__30+5;
              padding-bottom: 0;
            }
          }
          p{
            font-size: $__font__size__20;
            font-family: $__Punlicsans__Regular;
            color: $__white;
            line-height: 28px;
            @include respond-below(custom767){
              font-size: $__font__size__15;
              line-height: 25px;
              text-align: $__center;
            }
          }
          .shop-now-btn{
            text-decoration: $__none;
            font-size: $__font__size__15;
            font-family: $__Punlicsans__Bold;
            display: $__block;
            margin-top: 15px;
            padding: 15px 20px;
            background-color: rgba(40,116,240,1);
            color: $__white;
            border-radius: 8px;
          }
        }
        .banner-hero-col{
          img{
            max-width: $__full__width;
          }
        }
      }
    }
  }
/* End */

/* Product-Section */
  .product-img{
    .all-product-page{
      padding: 5% 0;
      .navication-to-home{
        padding: 50px 0px;
        ul{
          list-style: $__none;
          margin: 0;
          padding: 0;
          li{
            margin-right: 15px;
            display: $__inline__block;
            a{
              font-size: $__font__size__16;
              font-family: $__Punlicsans__Bold;
              color: $__black;
              text-decoration: $__none;
              @include respond-below(custom767){
                font-size: $__font__size__15;
              }
            }
            span{
              font-size: $__font__size__16;
              font-family: $__Punlicsans__Bold;
              color: $__black;
              @include respond-below(custom767){
                font-size: $__font__size__15;
              }
              strong{
                text-transform: capitalize;
                color: rgba(40,116,240,1);
              }
            }
          }
        }
      }
      .product-each{
        .inner-section{
          padding: 10px;
          position: $__relative;
          overflow: $__hidden;
          background-color: $__white;
          border-radius: 8px;
          padding: 15px;
          padding-top: 50px;
          transition: 0.5s ease;
          margin-bottom: 30px;
          border: 1px solid #8080801f;
          @include respond-below(custom992){
            margin-bottom: 25px;
          }
          &:hover{
            box-shadow: rgba(0,171,85,0.1) 0px 0px 2px 0px, rgba(0,171,85,0.1) 0px 16px 32px -4px;
            -webkit-box-shadow: rgba(0,171,85,0.1) 0px 0px 2px 0px, rgba(0,171,85,0.1) 0px 16px 32px -4px;
            -moz-box-shadow: rgba(0,171,85,0.1) 0px 0px 2px 0px, rgba(0,171,85,0.1) 0px 16px 32px -4px;
            border-color: $__primary__color;
          }
          .product{
            text-decoration: $__none;
            .icon{
              font-size: $__font__size__30;
              position: $__absolute;
              top: 15px;
              right: 15px;
              color: $__primary__color;
            }
            .product-img{
              height: 230px;
              object-fit: $__contain;
              width: $__full__width;
              margin: 0 $__auto;
            }
            .price{
              font-size: $__font__size__20;
              font-family: $__Punlicsans__Bold;
              color: $__primary__color;
              margin: 0;
              padding: 10px 0px;
              .lable{
                color: $__black;
                font-size: $__font__size__18;
              }
            }
            .product-rating{
              display: $__flex;
              align-items: $__center;
              .rate{
                margin: 0;
                width: 50px;
                height: 20px;
                background-color: $__primary__color;
                display: $__flex;
                padding: 5px;
                border-radius: 8px;
                margin-right: 10px;
                .rate-no{
                  font-size: $__font__size__15;
                  font-family: $__Punlicsans__Black;
                  color: $__white;
                  margin-right: 5px;
                }
                .rate-icon{
                  color: $__white;
                  display: $__block;
                }
              }
              .rating-count{
                .count-no{
                  font-size: $__font__size__15;
                  font-family: $__Punlicsans__Regular;
                }
              }
            }
            .product-name{
              font-size: $__font__size__17;
              font-family: $__Punlicsans__Regular;
              color: $__black;
              margin: 0;
              line-height: 20px;
              min-height: 75px;
              display: $__flex;
              align-items: self-start;
              text-align: $__left;
              padding: 10px 0;
            }
          }
        }
      }
    }
  }
/* End */

/* Single Product Section */
  .single-product-wrapper{
    .single-product{
      padding: 5% 0;
      .navication-to-home{
        padding: 50px 0px;
        ul{
          list-style: $__none;
          margin: 0;
          padding: 0;
          li{
            margin-right: 15px;
            display: $__inline__block;
            a{
              font-size: $__font__size__16;
              font-family: $__Punlicsans__Bold;
              color: $__black;
              text-decoration: $__none;
              @include respond-below(custom767){
                font-size: $__font__size__15;
              }
            }
            span{
              font-size: $__font__size__16;
              font-family: $__Punlicsans__Bold;
              color: $__black;
              @include respond-below(custom767){
                font-size: $__font__size__15;
              }
            }
          }
        }
      }
      .product-img-des-col{
        @include respond-below(custom992){
          padding: 0;
        }
        .img-section{
          display: $__flex;
          align-items: flex-start;
          
          @include respond-below(custom767){
            flex-wrap: wrap;
            justify-content: $__center;
            margin-bottom: 25px;
          }
          .sm-img{
            width: 25%;
            max-height: 152px;
            @include respond-below(custom767){
              order: 2;
              width: $__full__width;
              text-align: $__center;
            }
            img{
              max-width: 100%;
              height: 100px;
              width: 100px;
              padding: 5px;
              border: 1px solid gray;
              @include respond-below(custom992){
                width: $__auto;
              }
            }
          }
          .lg-img{
            width: 75%;
            @include respond-below(custom767){
              margin-bottom: 15px;
            }
            img{
              max-width: 100%;
            }
          }
        }
      }
      .description-col{
        padding-left: 50px;
        @include respond-below(custom767){
          padding-left: 0;
        }
        .description-section{
          .category{
            margin: 0;
            font-size: $__font__size__15;
            font-family: $__Punlicsans__Bold;
            color: $__black;
            text-transform: $__uppercase;
          }
          .product-name{
            font-size: $__font__size__20+5;
            font-family: $__Punlicsans__Bold;
            color: $__black;
            line-height: 25px;
            padding: 20px 0px;
            margin: 0;
            @include respond-below(custom767){
              font-size: $__font__size__20;
            }
          }
          .product-price{
            .price-pro{
              font-size: $__font__size__40+3;
              font-family: $__Punlicsans__Black;
              margin: 0;
              padding-bottom: 20px;
              color: $__black;
              display: $__flex;
              align-items: $__center;
              @include respond-below(custom767){
                font-size: $__font__size__35;
                padding-bottom: 10px;
              }
              span{
                display: $__flex;
              }
              .p-price{
                margin-left: -10px;
              }
            }
          }
          .rating{
            .product-rating{
              display: $__flex;
              align-items: $__center;
              .rate{
                margin: 0;
                width: 50px;
                height: 20px;
                background-color: $__primary__color;
                display: $__flex;
                padding: 5px;
                border-radius: 8px;
                margin-right: 10px;
                justify-content: $__center;
                align-items: $__center;
                .rate-no{
                  font-size: $__font__size__15;
                  font-family: $__Punlicsans__Black;
                  color: $__white;
                  margin-right: 5px;
                }
                .rate-icon{
                  color: $__white;
                  display: $__block;
                }
              }
              .rating-count{
                .count-no{
                  font-size: $__font__size__15;
                  font-family: $__Punlicsans__Regular;
                }
              }
            }
          }
          .description-p{
            font-size: $__font__size__15;
            font-family: $__Punlicsans__Regular;
            line-height: 25px;
          }
        }
        .add-cart-buy-button{
          padding: 20px 0px;
          .add-buy-btn{
            border: $__none;
            background-color: $__transparent;
            font-family: $__Punlicsans__Black;
            font-size: $__font__size__15;
            padding: 15px;
            text-transform: $__uppercase;
            border-radius: 8px;
            cursor: pointer;
            margin-right: 15px;
          }
          .add-cart{
            color: $__primary__color;
            background-image: url('../../images/shopping-cart1.png');
            background-repeat: no-repeat;
            background-size: 20px;
            background-position: center;
            background-position: 10%;
            padding-left: 45px;
            transition: 0.4s ease;
            &:hover{
              background-color: #57de9a;
              color: $__white;
            }
          }
          .buy-now{
            color: #b72136;
            background-image: url('../../images/payment-method.png');
            background-repeat: no-repeat;
            background-size: 20px;
            background-position: center;
            background-position: 10%;
            padding-left: 45px;
            transition: 0.4s ease;
            &:hover{
              background-color: #f37384;
              color: $__white;
            }
          }
        }
      }
    }
  }
/* End */